
import {Axios} from '../../utils/http'
import {sortFunction,parseWithUndefinedHandling} from '../../utils/common'
const state={
    envList:[],
    pageInfo:{
        pageSize:20,
        currentPage:1,
        pageCount:1,
        total:0,
    },
    searchContent:''
}
const getters = {}
const mutations = {}
const actions = {
    getEnvList(context:any,payload:any){
            let data:any
            data = {page:context.state.pageInfo.currentPage,
                size:context.state.pageInfo.pageSize,
                searchContent:context.state.searchContent,
                workspace_id:context.rootState.rightsManagement.curWorkspace.id,
                owner_id:parseWithUndefinedHandling(localStorage.getItem('user_id'))}
            return Axios({
                method:'post',
                url:'/envManager/getAll',
                data:data
            }).then((res) => {
                if(res.data.code == 10000){
                    context.state.envList = res.data.data.items
                    context.state.pageInfo.total = res.data.data.total
                    context.state.pageInfo.pageCount = Math.ceil(res.data.data.total / res.data.data.size)
                    sortFunction(context.state.envList,'updated_at',false)//按降序排序
                }else{
                    context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
                }
            }).catch((err) => {
                console.log('err',err)
            })
    },
    getEnvDetails(context:any,payload:any){
        return Axios({
            method:'get',
            url:`/envManager/getDetails/${payload.id}`,
        })
    },
    updateEnv(context:any,payload:any){
        return Axios({
            method:'post',
            url:`/envManager/update/${payload.id}`,
            data:payload
        })
    },
}
export default {
   namespaced:true,
   state,
   getters,
   mutations,
   actions
}