import {Axios} from '../../utils/http'
import {sortFunction,parseWithUndefinedHandling} from '../../utils/common'

const state={
   dataSourceList:[],
   allDataSourceList:[],
   pageInfo:{
      pageSize:20,
      currentPage:1,
      pageCount:1,
      total:0,
   },
}
const getters = {}
const mutations = {
   SET_DATA_SOURCE_LIST(state:any,payload:any){
      state.dataSourceList = payload
      sortFunction(state.dataSourceList,'updated_at',false)//按降序排序
      state.dataSourceList.forEach((item:any,index:number) => {
         item.connection_params.extra_params = JSON.stringify(parseWithUndefinedHandling(item.connection_params.extra_params),null,2) || ''
      })
   },
   SET_PAGE_INFO(state:any,{pageSize = state.pageInfo.pageSize,currentPage = state.pageInfo.currentPage,pageCount = state.pageInfo.pageCoun,total = state.pageInfo.total}:{ pageSize?: number, currentPage?: number, pageCount?: number, total?: number }){
      state.pageInfo = Object.assign(state.pageInfo,{pageSize,currentPage,pageCount,total})
   },
}
const actions = {
   getDataSourceList(context:any,payload:any){
      let data:any
      data = {page:context.state.pageInfo.currentPage,
               size:context.state.pageInfo.pageSize,
               searchContent:payload?.searchContent || '',
               workspace_id:context.rootState.rightsManagement.curWorkspace.id,
               owner_id:parseWithUndefinedHandling(localStorage.getItem('user_id'))}
      return  Axios({
         method:'post',
         url:'/datasource/getAll',
         data:data
      }).then((res) => {
         if(res.data.code == 10000){
            context.commit('SET_PAGE_INFO',{total:res.data.data.total,pageCount:Math.ceil(res.data.data.total / res.data.data.size)})
            context.commit('SET_DATA_SOURCE_LIST',res.data.data.items)
         }else{
            context.commit('SET_DATA_SOURCE_LIST',[])
            context.commit('SET_PAGE_INFO',{pageSize:10,currentPage:1,pageCount:1,total:0,})
         }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   getDataSourceDetails(context:any,payload:any){
      return Axios({
         method:'get',
         url:`/datasource/getDetails/${payload.id}`,
      })
   },
   createDataSource(context:any,payload:any){
      let data:any
      data = JSON.parse(JSON.stringify(payload.curDataSource))
      data.owner_id = parseWithUndefinedHandling(localStorage.getItem('user_id'))
      data.workspace_id = context.rootState.rightsManagement.curWorkspace.id
      return Axios({
         method:'post',
         url:'/datasource/create',
         data:data
      }).then((res) => {
         if(res.data.code == 10000){
            context.dispatch('getDataSourceList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
         }else if(res.data.code != 10034){
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
         }
         return res
      }).catch((err) => {
         console.log('err',err)
      })
   },
   deleteDataSource(context:any,payload:any){
      Axios({
         method:'delete',
         url:`/datasource/delete/${payload.id}`,
      }).then((res) => {
         if(res.data.code == 10005){
            const filteredDataSourceList = context.state.dataSourceList.filter((item:any) => {
               return item.id != payload.id
            })
            context.commit('SET_DATA_SOURCE_LIST',filteredDataSourceList)
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
         }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
         }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   updateDataSource(context:any,payload:any){
      return Axios({
         method:'post',
         url:`/datasource/update/${payload.curDataSource.id}`,
         data:payload.curDataSource
      }).then((res) => {
         if(res.data.code == 10007){
            context.dispatch('getDataSourceList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
         }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
         }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   getDBSchema(context:any,payload:any){
      return Axios({
         method:'get',
         url:`/datasource/get_db_schema/${payload.id}`
      })
   },
   getDataSourceType(){
      return Axios({
         method:'get',
         url:`/datasource/get_datasource_type`
      })
   }
}
export default {
   namespaced:true,
   state,
   getters,
   mutations,
   actions
}