/**
 * @description: 删除动态路由,退出登录时可调用
 * @param {object} router 路由实例
 * @param {Array} routes 公共路由数组对象
 * @return {*}
 */
const delete_asyncRoute = (router:any,routes:Array<RouteRecordRaw>) =>{
  const old_routes = router.getRoutes()
  old_routes.forEach((item:any) => {
     const name =item.name
     router.removeRoute(name)
  })
  //重新生成路由
  routes.forEach((route) => {
    router.addRoute(route)
  })
  router.push({name:'login'})
}
