import {Axios} from '../../utils/http'
import {sortFunction,parseWithUndefinedHandling} from '../../utils/common'


const state={
   dataModelingTaskList:[],
   allDataModelingTaskList:[],
   curDataModelingTask:{
      name: "",
      env_id:-1,
      operators:[
         {
            operatorType:0,
            name: "", 
            sourceDB: "", 
            sourceTable: "", 
            sourceProcessType: "stream", 
            readStartTime: "1999-11-11 00:00:00",
            data_dimensions:[], 
            metrics:[{"name": "", "function": "", "alias": ""}],
            processType: "stream"
         },
      ],
      description: "",
      owner_id: 1,
   },
   log:'',//日志
   pageInfo:{
      pageSize:20,
      currentPage:1,
      pageCount:1,
      total:0,
   },
   searchContent:''
}
const getters = {}
const mutations = {
   ADD_OPERATOR(state:any){
      state.curDataModelingTask.operators.push(chooseOperator(0))
   },
   DELETE_OPERATOR(state:any,payload:any){
      state.curDataModelingTask.operators.splice(payload.index,1)
   },
   CHANGE_OPERATOR(state:any,payload:any){
      state.curDataModelingTask.operators.splice(payload.index,1,chooseOperator(payload.type))
   },
   ADD_METRICS(state:any,payload:any){
      state.curDataModelingTask.operators[payload.index].metrics.push({name: "", function: "", alias: ""})
   },
   DELETE_METRICS(state:any,payload:any){
      if(state.curDataModelingTask.operators[payload.operatorsIndex].metrics.length>1){
         state.curDataModelingTask.operators[payload.operatorsIndex].metrics.splice(payload.metricsItem,1)
      }
   },
   INIT_LOG(state:any,payload:any){
      state.log = payload.log
   },
   ADD_OUTPUTCOLS(state:any,payload:any){
      if(!state.curDataModelingTask.operators[payload.index].outputCols){
         state.curDataModelingTask.operators[payload.index].outputCols = []
      }
      state.curDataModelingTask.operators[payload.index].outputCols.push({  
         tableName:'',
         columnName:'',
         aliasName:'',
      })
   },
   DELETE_OUTPUTCOLS(state:any,payload:any){
      if(state.curDataModelingTask.operators[payload.operatorsIndex].outputCols.length>1){
         state.curDataModelingTask.operators[payload.operatorsIndex].outputCols.splice(payload.OutputColsIndex,1)
      }
   },

}
const actions = {
   getdataModelingTaskList(context:any,payload?:any){
      let data:any
      data = {page:context.state.pageInfo.currentPage,
         size:context.state.pageInfo.pageSize,
         searchContent:context.state.searchContent,
         workspace_id:context.rootState.rightsManagement.curWorkspace.id,
         owner_id:parseWithUndefinedHandling(localStorage.getItem('user_id'))}
      return Axios({
         method:'post',
         url:'/dataModeling/getAll',
         data:data
      }).then((res) => {
            if(res.data.code == 10000){
            context.state.dataModelingTaskList = res.data.data.items
            context.state.pageInfo.total = res.data.data.total
            context.state.pageInfo.pageCount = Math.ceil(res.data.data.total / res.data.data.size)
            sortFunction(context.state.dataModelingTaskList,'updated_at',false)//按降序排序
         }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   getcurDataModelingTask(context:any,payload:any){
      Axios({
         method:'get',
         url:`/dataModeling/getDetails/${payload}`,
      })
   },
   createdataModelingTask(context:any){
      let data:any
      data = parseWithUndefinedHandling(JSON.stringify(context.state.curDataModelingTask))
      data.owner_id = parseWithUndefinedHandling(localStorage.getItem('user_id'))
      data.workspace_id = context.rootState.rightsManagement.curWorkspace.id
      Axios({
         method:'post',
         url:'/dataModeling/create',
         data:data
      }).then((res) => {
           if(res.data.code == 10000){
            context.dispatch('getdataModelingTaskList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   deletedataModelingTask(context:any,payload:any){
      Axios({
         method:'delete',
         url:`/dataModeling/delete/${payload.id}`,
      }).then((res) => {
           if(res.data.code == 10005){
            context.dispatch('getdataModelingTaskList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   updatedataModelingTask(context:any){
      Axios({
         method:'post',
         url:`/dataModeling/update/${context.state.curDataModelingTask.id}`,
         data:context.state.curDataModelingTask
      }).then((res) => {
           if(res.data.code == 10007){
            context.dispatch('getdataModelingTaskList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   getdataModelingStatus(context:any,payload:any){
      Axios({
         method:'get',
         url:`/dataModeling/getStatus/${payload.row.id}`,
      }).then((res) => {
           if(res.data.code == 10015){
            payload.row.status = res.data.data.status
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   startdataModelingTask(context:any,payload:any){
      Axios({
         method:'get',
         url:`/dataModeling/start/${payload.row.id}`,
      }).then((res) => {
           if(res.data.code == 10009){
            // payload.row.status = res.data.data.status
            context.dispatch('getdataModelingTaskList')
            // context.dispatch('getdataModelingStatus',payload)
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   stopdataModelingTask(context:any,payload:any){
      Axios({
         method:'get',
         url:`/dataModeling/stop/${payload.row.id}`,
      }).then((res) => {
           if(res.data.code == 10011){
            // payload.row.status = res.data.data.status
            context.dispatch('getdataModelingTaskList')
            // context.dispatch('getdataModelingStatus',payload)
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   viewLog(context:any,payload:any){
      return  Axios({
         method:'get',
         url:`/dataModeling/log/${payload.id}`,
      }).then((res) => {
            context.commit('INIT_LOG',{log:res.data})
      }).catch((err) => {
         console.log('err',err)
      })
   }

}
export default {
   namespaced:true,
   state,
   getters,
   mutations,
   actions
}

const chooseOperator = (type:number) => {
     switch(type){
        //groupby
         case 0:
            return {
                  operatorType:0,
                  name: "", 
                  sourceDB: "", 
                  sourceTable: "", 
                  readStartTime: "1999-11-11 00:00:00",
                  sinkTableName:"",
                  sourceProcessType: "stream", 
                  data_dimensions:[], 
                  metrics:[{name: "", function: "", alias: ""}],
                  // processType: "stream",
            }
            break;
         //join
         case 1:
            return {
                  operatorType:1, 
                  name: "", 
                  sourceDB: "", 
                  sourceTable: "", 
                  readStartTime: "1999-11-11 00:00:00", 
                  sinkTableName:"",
                  sourceProcessType: "stream", 
                  // processType: "stream",
                  rightTableName: "",  
                  joinType: "", 
                  conditionColumns: "", 
                  outputCols:[
                     {  
                        tableName:'',
                        columnName:'',
                        aliasName:'',
                     }
                  ]
            }
         //filter
         case 2:
            return {
                  operatorType:2, 
                  name: "", 
                  sourceDB: "", 
                  sourceTable: "", 
                  readStartTime: "1999-11-11 00:00:00",
                  sinkTableName:"",
                  sourceProcessType: "stream", 
                  // processType: "stream",
                  condititions: "", 
            }
         //countDistinct
         case 3:
            return {
                  operatorType:3, 
                  name: "", 
                  sourceDB: "", 
                  sourceTable: "", 
                  readStartTime: "1999-11-11 00:00:00", 
                  sinkTableName:"",
                  sourceProcessType: "stream", 
                  // processType: "stream",
                  columnName: "", 
                  rangeColumn: [], 
            }
         default: 
            return {
               operatorType:0,
               name: "", 
               sourceDB: "", 
               sourceTable: "", 
               readStartTime: "1999-11-11 00:00:00",
               sinkTableName:"",
               sourceProcessType: "stream", 
               // processType: "stream",
               data_dimensions:[], 
               metrics:[{"name": "", "function": "", "alias": ""}],
            }
     }
   }