import { isContext } from 'vm'
import {Axios} from '../../utils/http'
import {parseWithUndefinedHandling} from '../../utils/common'

const state={
    workspaceList:[],
    allWorkspaceList:[],
    curWorkspace:parseWithUndefinedHandling(sessionStorage.getItem('curWorkspace') || '{}'),
    leftMenu:[],
    roles:[],//当前工作空间的用户角色
    hasRoutes:false, //判断是否完成路由动态添加
    pageInfo:{
      pageSize:5,
      currentPage:1,
      pageCount:1,
      total:0,
    }
}
const getters = {}
const mutations = {
    INIT_LEFTMENU(state:any,{router}:any){
      state.leftMenu = []
      state.hasRoutes = true
      let index = 0;
      //初始化侧边栏meta.index
      router.getRoutes().forEach((item:any) => {
        if(item.path.indexOf('/home/') != -1 && item.meta.sideNav){
          item.meta.index =  index++
          state.leftMenu.push(item)
        }
      })
      //多个页面锁定同一个侧边目录
      router.getRoutes().filter((item:any) => {
        if((item.meta.permissionName == 'users' || item.meta.permissionName == 'taskPublishing' || item.meta.permissionName == 'dataLake' || item.meta.permissionName == 'dataLakeExport') && !item.meta.sideNav){
          item.meta.index = router.getRoutes().find((sideNavItem:any) => {return sideNavItem.meta.permissionName == item.meta.permissionName  && sideNavItem.meta.sideNav})?.meta.index
        }
      })
    },
    SET_PERMISSION(state:any,payload:any){
      // state.curWorkspace = JSON.parse(JSON.stringify(curWorkspace))
      state.curWorkspace = payload
      sessionStorage.setItem('curWorkspace',JSON.stringify(state.curWorkspace))
      state.roles = state.curWorkspace?.roles
    },
}
const actions = {
  async getPermission(context:any,payload:any){
    return Axios({
        method:'post',
        url:`/user/getPermission`,
        data:{
          user_id:parseWithUndefinedHandling(localStorage.getItem('user_id')),
          workspace_id:payload.workspace_id
        }
    }).then((res) => {
        if(res.data.code == 10000){
          context.commit('SET_PERMISSION',res.data.data.permission)
        }else{
          context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
        }
    }).catch((err) => {
        console.log('err',err)
    })
  },
  getWorkspaceList(context:any,payload:any){
    let data
    data = {page:context.state.pageInfo.currentPage,
        size:context.state.pageInfo.pageSize,
        // workspace_id:context.rootState.rightsManagement.curWorkspace.id,
        owner_id:parseWithUndefinedHandling(localStorage.getItem('user_id'))}
    if(payload?.all){//获取全部数据
        data.page = 0
        data.size = 0
    }
    return Axios({
      method:'post',
      url:'/workspace/getAll',
      data:data
   }).then((res) => {
        if(res.data.code == 10000){
          if(payload?.all){
            context.state.allWorkspaceList = res.data.data.items
          }else{
            context.state.workspaceList = res.data.data.items
            context.state.pageInfo.total = res.data.data.total
            context.state.pageInfo.pageCount = Math.ceil( res.data.data.total / context.state.pageInfo.pageSize)
          }
        }
        return res
   }).catch((err) => {
      console.log('err',err)
   })
  },
  createWorkspace(context:any,payload:any){
    //工作空间不考虑更新，name直接拼前缀
    let data = {name:payload.newWorkspace.prefix+payload.newWorkspace.name,user_id:payload.user_id}
    return Axios({
        method:'post',
        url:`/workspace/create`,
        data:data
     }).then((res) => {
          if(res.data.code == 10000){
            context.dispatch('getWorkspaceList',{all:true})
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
          }else if(res.data.code != 10036){
           context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
          }
          return res
     }).catch((err) => {
        console.log('err',err)
     }) 
  },
  deleteWorkspace(context:any,payload:any){
      Axios({
          method:'delete',
          url:`/workspace/delete/${payload.id}`
      }).then((res) => {
          if(res.data.code == 10005){
          context.dispatch('getWorkspaceList',{all:true})
          context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
          }else{
          context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
          }
    }).catch((err) => {
        console.log('err',err)
    })
  },
}
export default {
   namespaced:true,
   state,
   getters,
   mutations,
   actions
}
