<template>
    <div class="home">
        <TopNav/>
        <div class="content">
            <SideNav class="side-nav"/>
            <router-view  
                v-slot="{ Component }" 
                :key="viewKey"
                :class="[noPadding?'':'spacing']"
                style="width:100%;
                        height:100%;
                        box-sizing: border-box;
                        position:relative;
                        overflow: auto;">
            <keep-alive :include="['TaskStatisticsView']">
                <component :is="Component" />
            </keep-alive>
            </router-view>
        </div>
    </div>
</template>

<script setup>
import { ref, provide, computed } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import SideNav from '../components/SideNav.vue'
import TopNav from "../components/TopNav.vue";
import OpenNewWindow from '../components/OpenNewWindow'
const route = useRoute()
const router = useRouter()
const viewKey = ref(0)
const noPaddingPaths = ['/home/dataAnalyze','/home/zeppelin','/404','/license', '/home/dataIndex']
const refreshComponent = () => {
    viewKey.value = viewKey.value + 1 
}
const noPadding = computed(() => {
    return noPaddingPaths.includes(route?.path)
})
provide('refreshComponent',refreshComponent)
</script>
