import store from "@/store";
import { createRouter, createWebHistory,createWebHashHistory,RouteRecordRaw } from 'vue-router'
import {asyncRoutes} from './permission'
import HomeView from '../views/HomeView.vue'
import LoginView from '../views/LoginView.vue'
import LicenseView from '../views/LicenseView.vue'
import NotFoundView from '../views/NotFoundView.vue'
import TaskStatisticsView from '../views/TaskStatisticsView.vue'
import {parseWithUndefinedHandling} from '../utils/common'
import CasdoorLogin from '../views/CasdoorLogin.vue'


//公共路由，全部角色均具有该路由权限
const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name:'/',
    redirect:'/home',
  },
  {
    path: '/login',
    name: 'Login',
    component: CasdoorLogin,
  },
  {
    path: '/License',
    name: 'License',
    component: LicenseView
  },
  {
    path: '/home',
    name: 'home',
    component: HomeView,
    meta:{index:-1},
    children:[
      {    // redirect: 'home'//默认显示子路由组件
        path:'taskStatistics',
        name:'TaskStatistics',
        component:TaskStatisticsView,
        meta: { index:-1, sideNav:true, icon:'monitor',text:'作业监控'},
      },
      {
        path: 'about',
        name: 'About',
        component: () => import('../views/AboutView.vue'),
        meta:{},
      },
    ],
    redirect: '/home/taskStatistics'//默认显示子路由组件
  },
  {
    path: '/404',
    name: 'NotFound',
    component: NotFoundView,
    meta:{},
  },
  // {
  //   path: '/:catchAll(.*)',
  //   name:'404',
  //   redirect:'/404'
  // }
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})
const noInterceptionRoutes = ['/license','/404']
router.beforeEach(async (to,from,next) => {
  const isLoggedIn = !!localStorage.getItem('token');
    if (isLoggedIn) {
    //本地有登录信息
      if(to.path == '/login'){
        next('/')
      }else if(!noInterceptionRoutes.includes(to.path) && !store.state.rightsManagement.hasRoutes){//每次刷新都会执行
        try {
          await handleRefresh()
          //next({...to}) 会等待所有异步操作完成。这意味着页面的渲染会等待数据加载完毕后才开始
          next({ ...to, replace: true })
        }catch(error){
          next('/404')
          console.error(error);
        }
      }else{
        next()
      }
    }else {
    //还没有登录信息
      if(to.path == '/login'){
        next()
      }else{
        let params = new URLSearchParams(window.location.search);
        let [code] = [params.get('code')];
        if(code){
        //SSO
          try {
            store.dispatch('getSSOUser',{code:code})
          }catch(error){
            console.error(error)
          }
        }else{
        //其他路由
            next('/login')
        }
      }
    }
})

/**
 * @description:刷新时更新权限
 */ 
const updatePermission = async () => {
  return store.dispatch('rightsManagement/getWorkspaceList',{all:true}).then(async(res) => {
    if(res.data.code === 10000){
      let workspace_id = store.state.rightsManagement.allWorkspaceList[0]?.id
      if(parseWithUndefinedHandling(sessionStorage.getItem('curWorkspace'))){
        workspace_id = parseWithUndefinedHandling(sessionStorage.getItem('curWorkspace'))?.id
      }
      await store.dispatch('rightsManagement/getPermission',{workspace_id:workspace_id})
    }
  }).catch((err) => {
    console.log(err)
  })
}

/**
 * @description: 动态添加路由
 * @return {*}
 */
 export const addRights = async() => {
  const isLoggedIn = !!localStorage.getItem('token');
  resetRouter(router,routes)    
  if(isLoggedIn){
    if(store.state.rightsManagement.curWorkspace){
      const permission_list  = parseWithUndefinedHandling(JSON.stringify(store.state.rightsManagement.curWorkspace?.permission_list)) || []
      const allow_routes = allowRoutes(asyncRoutes,permission_list)
      addRoutes(allow_routes,router)
      //放在这儿防止页面刷新时404
      router.addRoute( {
        path: '/:catchAll(.*)',
        name:'404',
        redirect:'/404'
      })
    }
    store.commit('rightsManagement/INIT_LEFTMENU',{router})
  }
}

export const handleRefresh = async() => {
  await updatePermission()
  addRights()
}

/**
* @description: 过滤动态生成路由
 * @param {array} permission_list 后端返回的可访问路由名
 * @param {array} asyncRoutes 全部路由routes
 * @returns {array} allow_routes 过滤的可访问路由
 */
const allowRoutes = (asyncRoutes:Array<any>,permission_list:Array<any>):Array<any> => {
  // const allow_routes = asyncRoutes.filter((route)=>{ //过滤允许访问的路由
  //   if(permission_list){
  //     return Object.keys(permission_list).includes(route.meta.permissionName);
  //   }else{
  //     return false
  //   }
  // })
  // return allow_routes
  // 为了保证permission_list的顺序

  if(permission_list && Object.keys(permission_list)){
    let allow_routes = []
    for(let routeName of  Object.keys(permission_list)){
      for(let route of asyncRoutes){
        if(route.meta.permissionName == routeName){
          allow_routes.push(route)
        }
      }
    }
    return allow_routes
  }else{
    return []
  }
}

/**
 * @description: 添加动态生成路由在/home/路径下
 * @param {object} allow_routes 动态过滤出的准备添加的路由
 * @param {object} router 路由实例
 */
function addRoutes(allow_routes:Array<any>,router:any){
  allow_routes?.forEach((routeItem,index)=>{ // 将允许访问的路由动态添加到路由栈中
    router.addRoute('home',routeItem);
  })
}

/**
 * @description: 删除动态路由
 * @param {object} router 路由实例
 * @param {Array} routes 公共路由数组对象
 * @return {*}
 */
function resetRouter(router:any,routes:Array<RouteRecordRaw>){
  //将整个路由栈清空
  const old_routes = router.getRoutes();//获取所有路由信息
  old_routes.forEach((item:any) => {
    const name = item.name;//获取路由名词
    router.removeRoute(name || ''); //移除路由
  });

  //生成新的路由栈
  routes.forEach((route) => {
    router.addRoute(route);
  });
}

export default router
