import {Axios} from '../../utils/http'
import {sortFunction, parseWithUndefinedHandling, setInvalidLicense} from '../../utils/common'
const state={
  users:[],
  allUsers:[],
  pageInfo:{
      pageSize:5,
      currentPage:1,
      pageCount:1,
      total:0,
  },
  invalidLicenseInfo:{}//license失效时about页面的动态配置信息
}
const getters = {}
const mutations = {
    SET_USER_LIST(state:any,payload:any){
        state.users = payload
        sortFunction(state.users,'updated_at',false)//按降序排序
     },
    SET_PAGE_INFO(state:any,{pageSize = state.pageInfo.pageSize,currentPage = state.pageInfo.currentPage,pageCount = state.pageInfo.pageCoun,total = state.pageInfo.total}:{ pageSize?: number, currentPage?: number, pageCount?: number, total?: number }){
        state.pageInfo = Object.assign(state.pageInfo,{pageSize,currentPage,pageCount,total})
    },
    SET_INVALID_LICENSE(state:any,payload:number = 10020){
        state.invalidLicenseInfo = setInvalidLicense(payload)
    }
}
const actions = {
    getUsers(context:any,payload:any){
        let data
        data = {page:context.state.pageInfo.currentPage,
            size:context.state.pageInfo.pageSize,
            workspace_id:context.rootState.rightsManagement.curWorkspace.id,
            owner_id:parseWithUndefinedHandling(localStorage.getItem('user_id'))}
        if(payload?.all){//获取全部数据
            data.page = 0
            data.size = 0
        }
        Axios({
            method:'post',
            url:'/user/getAll',
            data:data
        }).then((res) => {
            if(res.data.code == 10000){
                if(payload?.all){
                    context.state.allUsers = sortFunction(res.data.data.items,'updated_at',false)
                }else{
                    context.commit('SET_USER_LIST',res.data.data.items)
                    context.commit('SET_PAGE_INFO',{total:res.data.data.total,pageCount:Math.ceil(res.data.data.total / res.data.data.size)})              
                }
            }else{
                context.commit('SET_USER_LIST',[])
                context.commit('SET_PAGE_INFO',{pageSize:10,currentPage:1,pageCount:1,total:0,})
            }
        }).catch((err) => {
            console.log('err',err)
        })
    },
    getUserDetails(context:any,payload:any){
        return Axios({
            method:'get',
            url:`/user/getDetails/${payload.id}`,
        })
    },
    createUser(context:any,payload:any){
        return Axios({
            method:'post',
            url:'/user/create',
            data:payload?.curUser
        }).then((res) => {
            if(res.data.code == 10000){
                context.dispatch('getUsers')
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
            }else if(res.data.code != 10035){
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
            }
            return res
        }).catch((err) => {
            console.log('err',err)
        })  
    },
    deleteUser(context:any,payload:any){
        Axios({
            method:'delete',
            url:`/user/delete/${payload.id}`
        }).then((res) => {
            if(res.data.code == 10005){
                context.dispatch('getUsers')
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
            }else{
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
            }
       }).catch((err) => {
            console.log('err',err)
       })
    },
    updateUser(context:any,payload:any){
        Axios({
            method:'post',
            url:`/user/update/${payload?.curUser?.id}`,
            data:payload?.curUser
         }).then((res) => {
                if(res.data.code == 10007){
                    context.dispatch('getUsers')              
                    context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
                }else{
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
            }
        }).catch((err) => {
            console.log('err',err)
        })
   },
   synchronizedUser(context:any,payload:any){
       Axios({
           method:'get',
           url:`/user/syncSsouser`,
        }).then((res) => {
            if(res.data.code == 10000){
                context.dispatch('getUsers')
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
            }else{
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
            }
        }).catch((err) => {
           console.log('err',err)
        })
   },
   // getUserUnderWorkspace(context:any,payload:any){
   //     Axios({
   //         method:'post',
   //         url:`/workspace/getUsers`,
   //         data:{workspace_id:payload.workspace_id,page:context.state.pageInfo.currentPage,size:context.state.pageInfo.pageSize}
   //     }).then((res) => {
   //         if(res.data.code == 10000){
   //             context.state.users = sortFunction(res.data.data.items,'updated_at',false)
   //             context.state.pageInfo.total = res.data.data.total
   //             context.state.pageInfo.pageCount = Math.ceil( res.data.data.total / context.state.pageInfo.pageSize)
   //         }
   //     }).catch((err) => {
   //         console.log('err',err)
   //     })
   // },
   //全局任务管理
    taskStatistics(context:any, payload:any){
        return Axios({
            method: 'post',
            url: '/user/jobStatistic',
            data: {
                start_time: payload.startTime,
                end_time: payload.endTime
            }
        })
    },
    getAboutInfo(context:any,payload:any){
        return Axios({
          method:'get',
          url:`/user/about`,
        })
    },
    //获取license状态码
    getLicenseType(){
        return Axios({
            method:'get',
            url:'user/license'
        })
    }
}
export default {
  namespaced:true,
  state,
  getters,
  mutations,
  actions
}