import store from "@/store";
import { Axios } from "@/utils/http";

const state = {};
const getters = {};
const mutations = {};

const actions = {
  async action_set_index(context: any, payload: any) {
    return Axios({
      method: 'post',
      url: '/index/createChart/',
      data: payload,
    })
      .then(async (res: any) => {
        // 重新获取树结构
        context.dispatch('action_set_dirs', { status: 'get', isNotInfoTip: true })
        store.commit('POP_MESSAGE', { type: 'success', message: '创建成功' });
        // 设置该指标meta
        context.dispatch('action_set_dir_nav_data', { id: payload.index_id, isNotInfoTip: true });
      })
      .catch((err: any) => {
        console.log('err:', err);
        store.commit('POP_MESSAGE', { type: 'error', message: '请求失败' });
        throw new Error(err)
      });
  },
};
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
};
