import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import * as echarts from 'echarts'
import ElementPlus from 'element-plus'
import zhCn from 'element-plus/dist/locale/zh-cn.mjs'
import * as ElementPlusIconsVue from '@element-plus/icons-vue'
import 'element-plus/dist/index.css'
import loading from './components/common/Loading/loading'
import './assets/css/common.scss' //全局样式
import SvgIcon from './components/SvgIcon.vue'
import importAllSvgIcons from '@/assets/svg/index'
// import "./mock"
import $ from "jquery"
import '@/assets/icons/iconfont.css'
importAllSvgIcons()

//解决el-table ResizeObserver loop limit exceeded问题
const debounce = (fn:any, delay:number) => {
  let timer:any = null;
  return function (this:any) {
    let context = this;
    let args = arguments;
    clearTimeout(timer);
    timer = setTimeout(function () {
      fn.apply(context, args);
    }, delay);
  }
}
const _ResizeObserver = window.ResizeObserver;
window.ResizeObserver = class ResizeObserver extends _ResizeObserver{
  constructor(callback:any) {
    callback = debounce(callback, 16);
    super(callback);
  }
}

const app = createApp(App)
for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  app.component(key, component)
}

//自定义指令
app.directive('permission',{
  mounted(el, binding, vnode, prevVnode){
    const permission = binding.value //获取绑定的值
    const path_name = router.currentRoute.value.name || '' //当前路由名
    if(store.state.rightsManagement.curWorkspace?.permission_list){
      const have_permission = store.state.rightsManagement.curWorkspace?.permission_list[path_name]
      if(have_permission && have_permission.includes(permission)){
        return
      }else{
        el.disabled = true
        // el.ariaDisabled = true
        el.classList.add('is-disabled');
      }
    }
  }
})
app.config.globalProperties.$echarts = echarts;
app.component('SvgIcon',SvgIcon)
app.use(store).use(router).use(ElementPlus,{locale:zhCn,}).use(loading).mount('#app')
