interface CookieOptions {
    expires?: number | Date;
    path?: string;
    domain?: string;
    secure?: boolean;
    httponly?:boolean;
}
class CookieManager{
    public static setCookie(name:string, value:string,options:CookieOptions = {}): void{
        const {expires = 1, path = '/', domain, secure, httponly = false} = options
        let cookieString = encodeURIComponent(name) + '=' + encodeURIComponent(value)
        if(expires){
            const expirationDate = new Date()
            if(typeof expires === 'number'){
                expirationDate.setDate(expirationDate.getDate() + expires)
            } else {
                expirationDate.setTime(expires.getTime())
            }    
            cookieString += `; expires=${expirationDate.toUTCString()}`
        }
        if(path){
            cookieString += `; path=${path}`
        }
        if (domain) {
            cookieString += `; domain=${domain}`;
        }
        if (secure) {
            cookieString += '; secure';
        }
        if (httponly) {
            cookieString += '; httponly';
        }
        document.cookie = cookieString
    }

    public static getCookie(name:string): string | null{
        const cookies = document.cookie.split(';')
        for(const cookie of cookies){
            const [cookieName, cookieValue] = cookie.split('=')
            if(decodeURIComponent(cookieName.trim()) === name){
                return decodeURIComponent(cookieValue)
            }
        }
        return null
    }

    public static deleteCookie(name:string, options:CookieOptions = {}):void{
        const { path = '/', domain } = options;
        if (this.getCookie(name)) {
        document.cookie = `${encodeURIComponent(name)}=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=${path}${domain ? `; domain=${domain}` : ''}`;
        }
    }
}
export default CookieManager