export default function importAllSvgIcons() {
  try {
    // const request: __WebpackModuleApi.RequireContext = require.context('../svg', false, /\.svg$/)
    const svgContext = require.context('../svg', false, /\.svg$/);
    const unDrawSvgContext = require.context('../svg/unDraw', false, /\.svg$/);
    const requestAll = (requireContextArr:Array<__WebpackModuleApi.RequireContext>) => {
      requireContextArr.forEach((requireContext:__WebpackModuleApi.RequireContext) => {
        requireContext.keys().forEach(requireContext)
      })
    }
    // request.keys().forEach(request)
    requestAll([svgContext,unDrawSvgContext])
  } catch(err) {
    console.log(err)
  }
}