import { createStore } from 'vuex'
import { ElMessage } from 'element-plus'
import {TypeRootState} from './interface'
import dataSource from './modules/dataSource'
import dataLake from './modules/dataLake'
import dataLakeExport from './modules/dataLakeExport'
import dataModeling from '././modules/dataModeling'
import dataAnalyze from './modules/dataAnalyze'
import rightsManagement from './modules/rightsManagement'
import role from './modules/role'
import users from './modules/users'
import sqlSelfService from './modules/sqlSelfService'
import taskPublishing from './modules/taskPublishing'
import environment from './modules/environment'
import dataIndex from "./modules/dataIndex/index"

import router from '../router/index'
import { Axios,setBlockRequest } from '../utils/http'
import {sortFunction,transformMesssage } from '../utils/common'
import {handleRefresh} from '../router'
import CookieManager from '../utils/cookie'

export default createStore<TypeRootState>({
  state: {},
  getters: {
  },
  mutations: {
    POP_MESSAGE(state, payload) {
      ElMessage({
        dangerouslyUseHTMLString:true,
        message: transformMesssage(payload.message),
        type: payload.type,
      })
    },
    SORT_BY_DATE(state,payload){
      sortFunction(payload,'created_at',false)//按降序排序
    },
    CLEAR_CACHE(state){
      localStorage.removeItem('userName')
      localStorage.removeItem('token')
      localStorage.removeItem('user_id')
      sessionStorage.removeItem('curWorkspace')
      state.rightsManagement.hasRoutes = false 
    }
  },
  actions: {
    login({ commit,dispatch }, payload) {
      return Axios({
        method: 'post',
        url: 'user/login',
        data: {
          name: payload.name,
          password: payload.password,
        }
      }).then(async (res) => {
        if (res.data.code == 10017) {
          localStorage.setItem('userName', payload.name)
          localStorage.setItem('token', res.data.data.token)
          localStorage.setItem('user_id',res.data.data.user_id)
          router.replace({path:'/'})
        } else {
          commit('POP_MESSAGE', { message: res.data.message, type: 'error' })
        }
      }).catch((err) => {
        console.log('err', err)
      })
    },
  //casdoor登录
    getSSOUser({commit, dispatch},payload){
      return Axios({
        method: 'get',
        url: `user/getSsouser/${payload.code}`,
      }).then(async (res) => {
        if (res.data.code == 10017) {
          localStorage.setItem('userName',res.data.data.name)
          localStorage.setItem('token', res.data.data.token)
          localStorage.setItem('user_id',res.data.data.user_id)
          router.replace({ path: "/" })
        } else {
          commit('POP_MESSAGE', { message: res.data.message, type: 'error' })
          router.push({path: '/login'})
        }
      }).catch((err) => {
        console.log('err', err)
        router.push({path: '/login'})
      })
    },
    //casdoor部署地址
    getSSORedirectUrl({ commit }, payload){
      return Axios({
        method: 'get',
        url: 'user/getRedirectUri',
      })
    },
    license({ commit }, payload) {
      return Axios({
        method: 'post',
        url: '/user/verifyLicense',
        data: {
          name: localStorage.getItem('userName'),
          license: payload.license
        }
      }).then(async(res) => {
        if (res.data?.code == 10019) {
          commit('POP_MESSAGE', { message: res.data.message, type: 'success' })
          setBlockRequest(false)
          await handleRefresh()
          router.push({ path: "/" });
        } else {
          commit('POP_MESSAGE', { message: res.data.message, type: 'error' })
        }
      }).catch((err) => {
        console.log('err', err)
      })
    },
    signOut({commit,state}) {
      return Axios({
        method:'get',
        url: '/user/logout',
      }).then((res) => {
        if (res.data.code == 10025) {
          commit('CLEAR_CACHE')
          CookieManager.deleteCookie('casdoor_session_id')
          router.push({
            path: '/login'
          })
        } else {
          commit('POP_MESSAGE', { message: res.data.message, type: 'error' })
        }
      }).catch((err) => {
        console.log('err', err)
      })
    },
    getGrafanaUrL(){
      return Axios({
          method:'get',
          url:`jobManager/jobMonitoring`,
      })
  }
  },
  modules: {
    dataSource,
    dataLake,
    dataLakeExport,
    dataModeling,
    dataAnalyze,
    rightsManagement,
    role,
    users,
    sqlSelfService,
    taskPublishing,
    environment,
    dataIndex
  }
})