import axios from 'axios';
import { Axios } from '@/utils/http';
import store from '@/store';

const state = {
  index_meta_data_all: {},
};

const getters = {};

const mutations = {
  set_dir_nav_datas(state: any, payload: { id: string; data: any }) {
    // 只需要传该项的id和数据
    state.index_meta_data_all = {
      ...state.index_meta_data_all,
      [payload.id]: payload.data,
    };
  },
};

const actions = {
  action_set_dir_nav_data(
    ctx: any,
    payload: { id: string; refresh?: boolean; loading_type?: 'all', isNotInfoTip?: boolean }
  ) {
    // 首先要判断该激活项的数据是否存在
    // 如果存在，判断refresh是否要重新请求，true为重新请求
    if (ctx.state.index_meta_data_all[payload.id] && !payload.refresh) {
      // 当数据存在并且不刷新，则不请求
      return true;
    }

    return Axios({
      method: 'get',
      url: `/index/getIndexMeta/${payload.id}`,
    })
      .then((res: any) => {
        if (!res.data?.data || JSON.stringify(res.data?.data) === '{}') {
          // 没有meta
          return false;
        } else {
          // 有meta，说明创建过图表了，直接赋值
          ctx.commit('set_dir_nav_datas', {
            id: payload.id,
            data: {
              id: payload.id,
              data: res.data?.data, // data和meta的值相同
              meta: res.data?.data,
            },
          });
          !payload.isNotInfoTip && store.commit('POP_MESSAGE', { type: 'success', message: '成功获取指标详情' });
          return true;
        }
      })
      .catch((err: any) => {
        console.log('err:', err);
        store.commit('POP_MESSAGE', { type: 'error', message: '获取指标详情失败' });
        throw new Error(err);
      });
  },
};

export default {
  state,
  getters,
  mutations,
  actions,
};
