import {Axios} from '../../utils/http'

const state={
  time:5,//轮询时间，单位分钟
  echartType:'bar',
  chartError:true
}
const getters = {}
const mutations = {
    CHANGE_CHARTERROR(state:any,payload:boolean){
        state.chartError = payload
    }
}
const actions = {
    chartByExecuteSql(context:any,payload:any){
      return  Axios({
         method:'post',
         url:`/dataAnalysis/executeSql/${payload.id}`,
         data:{
            operatorType:payload?.operatorType,
            data_cols:payload?.data_cols
         }
      }).then((res) => {
           if(res.data.code == 10013){
            sessionStorage.setItem('chartInfo',JSON.stringify(res.data.data))
            context.commit('CHANGE_CHARTERROR',false)
            //缓存数据库和表名
            let curOperators = payload.row.operators
            let sqlFormData = { sourceDB:curOperators[0].sourceDB,sourceTable:payload.row.name,}
            sessionStorage.setItem('sqlFormData',JSON.stringify(sqlFormData))
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
            context.commit('CHANGE_CHARTERROR',true)
           }
           return res
      }).catch((err) => {
        context.commit('CHANGE_CHARTERROR',true)
        console.log('err',err)
      })
    },
    chartByRawSql(context:any,payload:any){
        return  Axios({
        method:'post',
        url:`/dataAnalysis/rawSql`,
        data:{
            rawSql:payload.rawSql,
            env_id:payload.env_id
        }
        }).then((res) => {
            if(res.data.code == 10013){
                context.commit('CHANGE_CHARTERROR',false)
            }else{
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
                context.commit('CHANGE_CHARTERROR',true)
            }
            return res
        }).catch((err) => {
            context.commit('CHANGE_CHARTERROR',true)
            console.log('err',err)
        })
    },
    chartByText2sql(context:any,payload:any){
        return  Axios({
            method:'post',
            url:`/dataAnalysis/text2sql`,
            data:{
                text:payload.text,
                table_name:payload.table_name,
                env_id:payload.env_id
            }
        })
    },
    getTablesByEnv(context:any,payload:any){
        return  Axios({
            method:'get',
            timeout: 120000,
            url:`dataAnalysis/getTablesByEnv/${payload.envID}`,
        })
    }

}
export default {
   namespaced:true,
   state,
   getters,
   mutations,
   actions
}