<template>
    <div ref="starsContainer" :class="['container',props?.moon?'moon':'']" >
        <div v-for="(star, index) in stars" :key="index" class="star" :style="star.style"></div>
    </div>
</template>
<script setup>
import { ref, onMounted } from 'vue'
const props = defineProps(['moon'])
const starsContainer = ref(null)
const stars = ref([])
onMounted(() => {
    generateStars()
})
const generateStars = () => {
    const bodyWidth = document.body.clientWidth;
    const bodyHeight = document.body.clientHeight;

    for (let i = 0; i < 60; i++) {
        const star = {
            style:{
                left: `${Math.random() * bodyWidth - 10 }px`,
                top: `${Math.random() * bodyHeight - 10}px`,
                transform: `scale(${Math.random() * 0.8})`,
                animationDelay: `${Math.random()}s`            
            }
        }
        stars.value.push(star);
      }
}
</script>
<style scoped lang="scss">
.container{
    width:100%;
    height:100%;
    position: relative;
    background-color:#03162a;
    .star {
        position: absolute;
        border-color: yellow transparent transparent transparent;
        border-width: 4.49px 6.18px;
        border-style: solid;
        animation: twinkle 2s infinite;
    }
}
.star:hover{
    transform: scale(2, 2) rotate(180deg) !important;
    transition: all 1s;
}
.star::before {
    content: '';
    display: block;
    position: absolute;
    left: -6.18px;
    top: -4.49px;
    border-color: white transparent transparent transparent;
    border-width: 4.49px 6.18px;
    border-style: solid;
    transform: rotate(72deg);
    transform-origin: 50% 22.5%;
}

.star::after {
    content: '';
    display: block;
    position: absolute;
    left: -6.18px;
    top: -4.49px;
    border-color: white transparent transparent transparent;
    border-width: 4.49px 6.18px;
    border-style: solid;
    transform: rotate(-72deg);
    transform-origin: 50% 22.5%;
}
.moon{
    background-image: url('../assets/img/lunar.png');
    background-repeat: no-repeat;
    background-position: right -80px bottom -150px;
    background-size: 300px 300px;
}
@keyframes twinkle {
    0%,100% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
}
</style>