/**
 * 打平二级数组
 * @param arr
 * @param allArr
 * @returns
 */
const steamroller = (arr: any, allArr: any) => {
  if (!arr.length) {
    return allArr;
  }
  arr.forEach((it: any) => {
    it.children.forEach((item: any) => {
      allArr.push(item);
    });
  });
  return allArr;
};
export default {
  state,
  getters,
  mutations,
  actions,
};
