import {Axios} from '../../utils/http'
import {sortFunction,parseWithUndefinedHandling} from '../../utils/common'

const state={
    roleList:[],//自定义用户列表
    allRoleList:[],
    pageInfo:{
        pageSize:5,
        currentPage:1,
        pageCount:1,
        total:0,
    }
}
const getters = {}
const mutations = {
    INIT_LEFTMENU(state:any,{router}:any){
      state.leftMenu = router.getRoutes().filter((item:any) => {
        return (item.path.indexOf('home/') != -1) && item.meta.sideNav
      })
    },
    SET_ROLE_LIST(state:any,payload:any){
        state.roleList = payload
        sortFunction(state.roleList,'updated_at',false)//按降序排序
     },
    SET_PAGE_INFO(state:any,{pageSize = state.pageInfo.pageSize,currentPage = state.pageInfo.currentPage,pageCount = state.pageInfo.pageCoun,total = state.pageInfo.total}:{ pageSize?: number, currentPage?: number, pageCount?: number, total?: number }){
        state.pageInfo = Object.assign(state.pageInfo,{pageSize,currentPage,pageCount,total})
    }
   
}
const actions = {
    getRoles(context:any,payload:any){
        let data
        data = {page:context.state.pageInfo.currentPage,
            size:context.state.pageInfo.pageSize,
            workspace_id:context.rootState.rightsManagement.curWorkspace.id,
            owner_id:parseWithUndefinedHandling(localStorage.getItem('user_id'))}
        if(payload?.all){//获取全部数据
            data.page = 0
            data.size = 0
        }
        return Axios({
            method:'post',
            url:'/role/getAll',
            data:data
        }).then((res) => {
            if(res.data.code == 10000){
                if(payload?.all){//获取全部数据
                    context.state.allRoleList = res.data.data.items
                }else{
                    context.commit('SET_ROLE_LIST',res.data.data.items)
                    context.commit('SET_PAGE_INFO',{total:res.data.data.total,pageCount:Math.ceil(res.data.data.total / res.data.data.size)})              
                }
            }else{
                context.commit('SET_ROLE_LIST',[])
                context.commit('SET_PAGE_INFO',{pageSize:10,currentPage:1,pageCount:1,total:0,})
            }
        }).catch((err) => {
            console.log('err',err)
        })
    },
    getRoleDetails(context:any,payload:any){
        return Axios({
            method:'get',
            url:`/role/getDetails/${payload.id}`,
        })
    },
    createRole(context:any,payload:any){
        let data
        data = JSON.parse(JSON.stringify(payload?.curRole))
        data.owner_id = parseWithUndefinedHandling(localStorage.getItem('user_id'))
        data.workspace_id = context.rootState.rightsManagement.curWorkspace.id
        Axios({
            method:'post',
            url:'/role/create',
            data:data
        }).then((res) => {
              if(res.data.code == 10000){
               context.dispatch('getRoles')
               context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
              }else{
               context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
              }
        }).catch((err) => {
            console.log('err',err)
        }) 
    },
    deleteRole(context:any,payload:any){
        Axios({
            method:'delete',
            url:`/role/delete/${payload.id}`
        }).then((res) => {
            if(res.data.code == 10005){
             context.dispatch('getRoles')
             context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
            }else{
             context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
            }
       }).catch((err) => {
          console.log('err',err)
       })
    },
    updateRole(context:any,payload:any){
        Axios({
            method:'post',
            url:`/role/update/${payload.id}`,
            data:payload
         }).then((res) => {
              if(res.data.code == 10007){
               context.dispatch('getRoles')
               context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
              }else{
               context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
              }
         }).catch((err) => {
            console.log('err',err)
         })
    }
}
export default {
   namespaced:true,
   state,
   getters,
   mutations,
   actions
}
