// import Vue from 'vue'
import { ElLoading } from 'element-plus'

let loading:any = null

const Loading = {
startLoading : () => {
    loading = ElLoading.service({
        text:'加载中...',
        spinner:'',
        background:'rgba(0, 0, 0, 0.7)'
    })
},

endLoading : () => {
    loading.close()
}
}


export default {
    install: (app:any) => {
        app.config.globalProperties.$loading = Loading
        // if(!Vue.$loading){
        //     Vue.$loading =  Loading
        // }
        // Vue.mixin({
        //     create(){
        //         this.$loading = Vue.$Loading
        //     }
        // })
    }
}