import {Axios} from '../../utils/http'
import {sortFunction,parseWithUndefinedHandling} from '../../utils/common'
const state={
   dataLakeList:[],
   allDataLakeList:[],
   pageInfo:{
      pageSize:20,
      currentPage:1,
      pageCount:1,
      total:0,
   },
   //调度历史分页
   jobsPageInfo:{
      pageSize:10,
      currentPage:1,
      pageCount:1,
      total:0,
   },
   env_id:-1,
   historyJobs:[],//批次任务调度历史
}
  
const getters = {}
const mutations = {  
   SET_DATA_LAKE_LIST(state:any,payload:any){
      state.dataLakeList = payload
      sortFunction(state.dataLakeList,'updated_at',false)//按降序排序
   },
   SET_PAGE_INFO(state:any,{pageSize = state.pageInfo.pageSize,currentPage = state.pageInfo.currentPage,pageCount = state.pageInfo.pageCoun,total = state.pageInfo.total}:{ pageSize?: number, currentPage?: number, pageCount?: number, total?: number }){
      state.pageInfo = Object.assign(state.pageInfo,{pageSize,currentPage,pageCount,total})
   }
}
const actions = {
   getDataLakeTaskList(context:any,payload:any){
      let data:any
      data = {page:context.state.pageInfo.currentPage,
         size:context.state.pageInfo.pageSize,
         searchContent:payload?.searchContent || '',
         workspace_id:context.rootState.rightsManagement.curWorkspace.id,
         owner_id:parseWithUndefinedHandling(localStorage.getItem('user_id'))}
      Axios({
         method:'post',
         url:'/datalake/getAll',
         data:data
      }).then((res) => {
         if(res.data.code == 10000){
            context.commit('SET_DATA_LAKE_LIST',res.data.data.items)
            context.commit('SET_PAGE_INFO',{total:res.data.data.total,pageCount:Math.ceil(res.data.data.total / res.data.data.size)})
         }else{
            context.commit('SET_DATA_LAKE_LIST',[])
            context.commit('SET_PAGE_INFO',{pageSize:10,currentPage:1,pageCount:1,total:0,})
         }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   getDataLakeDetails(context:any,payload:any){
      return Axios({
         method:'get',
         url:`/datalake/getDetails/${payload.id}`,
      })
   },
   createDataLakeTask(context:any,payload:any){
      let data:any
      data = JSON.parse(JSON.stringify(payload.curDataLake))
      data.owner_id = parseWithUndefinedHandling(localStorage.getItem('user_id')!)
      data.workspace_id = context.rootState.rightsManagement.curWorkspace.id
      return Axios({
         method:'post',
         url:'/datalake/create',
         data:data
      }).then((res) => {
           if(res.data.code == 10000){
            context.dispatch('getDataLakeTaskList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
           return res
      }).catch((err) => {
         console.log('err',err)
      })
   },
   deleteDataLakeTask(context:any,payload:any){
      Axios({
         method:'delete',
         url:`/datalake/delete/${payload.id}`,
      }).then((res) => {
           if(res.data.code == 10005){
            const filteredDataLakeList = context.state.dataLakeList.filter((item:any) => {
               return item.id != payload.id
            })
            context.commit('SET_DATA_LAKE_LIST',filteredDataLakeList)
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   updateDataLakeTask(context:any,payload:any){
      Axios({
         method:'post',
         url:`/datalake/update/${payload.curDataLake.id}`,
         data:payload.curDataLake
      }).then((res) => {
           if(res.data.code == 10007){
            context.dispatch('getDataLakeTaskList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   getDataLakeStatus(context:any,payload:any){
      Axios({
         method:'get',
         url:`/datalake/getStatus/${payload.row.id}`,
      }).then((res) => {
           if(res.data.code == 10015){
            payload.row.status = res.data.data.status
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   startDataLakeTask(context:any,payload:any){
      Axios({
         method:'get',
         url:`/datalake/start/${payload.row.id}`,
      }).then((res) => {
           if(res.data.code == 10009){
            // payload.row.status = res.data.data.status
            // context.dispatch('getDataLakeStatus',payload)
            context.dispatch('getDataLakeTaskList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   stopTask(context:any,payload:any){
      Axios({
         method:'get',
         url:`/datalake/stop/${payload.row.id}`,
      }).then((res) => {
           if(res.data.code == 10011){
            // payload.row.status = res.data.data.status
            // context.dispatch('getDataLakeStatus',payload)
            context.dispatch('getDataLakeTaskList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   resumeTask(context:any,payload:any){
      Axios({
         method:'get',
         url:`/datalake/resume/${payload.row.id}`,
      }).then((res) => {
           if(res.data.code == 10029){
            context.dispatch('getDataLakeTaskList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   viewLog(context:any,payload:any){
      return  Axios({
         method:'get',
         url:`/datalake/log/${payload.id}`,
      })
   },
   getHistoryJobs(context:any,payload:any){
      return  Axios({
         method:'post',
         url:`/datalake/getHistoryJobs`,
         data:{
            id:payload.id,
            page:context.state.jobsPageInfo.currentPage,
            size:context.state.jobsPageInfo.pageSize,
         }
      }).then((res) => {
         context.state.historyJobs = res.data.data.items
         context.state.jobsPageInfo.total = res.data.data.total
         context.state.jobsPageInfo.pageCount = Math.ceil(res.data.data.total / res.data.data.size)
      }).catch((err) => {
         console.log('err',err)
      })
   },
   retryHistoryJob(context:any,payload:any){
      return  Axios({
         method:'get',
         url:`/datalake/retryHistoryJob/${payload.id}`,
      })
   },
   getHistoryJobLog(context:any,payload:any){
      return  Axios({
         method:'post',
         url:`/datalake/historyJobLog/`,
         data:{
            job_id: payload.job_id,
            app_id: payload.app_id
         }
      })
   },
}
export default {
   namespaced:true,
   state,
   getters,
   mutations,
   actions
}