//表单嵌套检测
const formValidate = (formEl: FormInstance) => {
    if (!formEl) return
    return new Promise((resolve,reject) => {
        formEl.validate(async (valid, fields) => {
            if (valid) {
               resolve('success')
            } else {
               reject('err')
            }
        })
    })
}
export const submitForm  = async (refList:Array<FormInstance>) => {
    let allPromise:any = []
    refList.forEach((item:any) => {
        allPromise.push(formValidate(item))
    })
    return Promise.all(allPromise)
}

//按日期排序
export const sortFunction = ((arr:Array<any>,key:string,boolean:boolean) => {
    if(arr && arr?.length){
        function compare(key:any,boolean:any):any{
            return function(a:any,b:any){//返回比较方法
                let val1 = a[key]
                let val2 = b[key]
                 if(boolean){
                     //升序
                     return Date.parse(val1) - Date.parse(val2)
                 }else {
                     //降序
                     return Date.parse(val2) - Date.parse(val1)
                 }
            }
        }
        return arr.sort(compare(key,boolean))
    }
    return arr
}) 

