export const transformTimeFormat = (time:any) => {
  if(time){
    const tmp = new Date(time)
    const year = tmp.getFullYear()
    const month = tmp.getMonth() + 1 < 10 ? `0${tmp.getMonth() + 1}` : tmp.getMonth() + 1
    const day = tmp.getDate() < 10 ? `0${tmp.getDate()}` : tmp.getDate()
    const hour = tmp.getHours() <10 ? `0${tmp.getHours()}` : tmp.getHours()
    const minutes = tmp.getMinutes() <10 ? `0${tmp.getMinutes()}` : tmp.getMinutes()
    const second = tmp.getSeconds() <10 ? `0${tmp.getSeconds()}` : tmp.getSeconds()
    return `${year}-${month}-${day} ${hour}:${minutes}:${second}`
  }else{
      return null
  }
}
//防抖
export const debounce = (fn:Function,wait:number = 1000) => {
    let timer:any = null;
    return function(){
      if(timer != null) {
        clearTimeout(timer)
        timer = null
      }
      timer = setTimeout(() => {
        fn()
      },wait)
    }
  }

//序列化错误返回信息
export const transformMesssage = (message:any) => {
    if(typeof message == 'object'){
        message = JSON.stringify(message)
    }
    message.replace(/\n/g, "<br>")
    return message
}
//反序列化遇到字符串undeined时
export const parseWithUndefinedHandling = (serializedData:string | null) => {
    try {
        if(serializedData && serializedData !== 'undefined') {
            return JSON.parse(serializedData);
        }
    } catch (error) {
        console.error('Error parsing data:', error);
    }
    return undefined;
}

