//任务名正则检测
export const checkName = (rule: any, value: any, callback: any) => {
    // 校验规则
    const maxLength = value.length <= 253;
    const startsWithAlphanumeric = /^[a-z0-9]/.test(value);
    const endsWithAlphanumeric = /[a-z0-9]$/.test(value);
    const validCharacters = /^[a-z0-9.-]+$/.test(value);
    const validSubdomains = /^[a-z0-9]([-a-z0-9]*[a-z0-9])?(\.[a-z0-9]([-a-z0-9]*[a-z0-9])?)*$/.test(value);
  
    // 根据校验结果给出具体错误提示
    if (!maxLength) {
      callback(new Error('名称不能超过 253 个字符'))
    } else if (!startsWithAlphanumeric || !endsWithAlphanumeric) {
      callback(new Error('名称必须以小写字母数字开头和结尾'))
    } else if (!validCharacters) {
      callback(new Error('名称只能包含小写字母、数字，以及 "-" 和 "."'))
    } else if (!validSubdomains) {
      callback(new Error('DNS子域名多级使用 “.” 连接，每一级以小写字母或数字开头结尾，“-”只能出现在每一级的中间'))
    } else {
      callback()
    }
}
export const checkJSON = (rule: any, value: any, callback: any) => {
    if(value === ''){
        callback()
    }
    try{
        if(value && typeof value === 'string'){
            JSON.parse(value)//检测JSON
            callback()
        }else{
            callback(new Error('Invalid JSON format'))
        }
    }catch (error) {
        callback(new Error('Invalid JSON format')); // 格式无效，执行回调并传递错误信息
    }
}
