/**
 * 防抖
 * @param func
 * @param delayTime
 * @returns
 */
export const debounceWithParam = (func: Function, delayTime: number) => {
var timer: any;
return function () {
    clearTimeout(timer);
    var argus = arguments;
    timer = setTimeout(function () {
    func.apply(null, argus);
    }, delayTime);
};
};
/**
 * 获取两个数字之间的数组
 * 1-3之间：[1, 2, 3]
 * @param start
 * @param end
 * @returns
 */
export const getArrRange = (start: number, end: number) => {
return Array.from({ length: end - start + 1 }, (_, index) => index + start);
};

/**
 * 格式化时间(2023-08-20)
 * @param timestamp
 * @param timeFormat 传入的格式
 * @returns
 */
export const formatTimestamp = (
timestamp: number,
timeFormat: Time_Format = 'YYYY-MM-DD'
) => {
const date = new Date(timestamp);
let year = date.getFullYear() + '';
let month = date.getMonth() + 1 + '';
month = month.padStart(2, '0');
let day = date.getDate() + '';
day = day.padStart(2, '0');
let result = '';
result = timeFormat.replace(/YYYY/g, year);
result = result.replace(/MM/g, month);
result = result.replace(/DD/g, day);
if (!Time_Format_Arr.includes(timeFormat)) {
    return year + '-' + month + '-' + day;
}
return result;
};
/**
 * 数字转成中文
 * @param section
 * @returns
 */
export const sectionToChinese = (section: any) => {
const chnNumChar = [
    '零',
    '一',
    '二',
    '三',
    '四',
    '五',
    '六',
    '七',
    '八',
    '九',
];
const chnUnitChar = ['', '十', '百', '千'];
let strIns = '',
    chnStr = '';
let unitPos = 0;
let zero = true;

while (section > 0) {
    let v = section % 10;
    if (v === 0) {
    if (!zero) {
        zero = true;
        chnStr = chnNumChar[v] + chnStr;
    }
    } else {
    zero = false;
    strIns = chnNumChar[v];
    strIns += chnUnitChar[unitPos];
    chnStr = strIns + chnStr;
    }
    unitPos++;
    section = Math.floor(section / 10);
}

return chnStr;
}

/**
 * 数学公式格式校验
 * @param formulaInfo
 * @returns
 */
export const formulaInfojy = (formulaInfo: any) => {
var msg = [];
// 剔除空白符
formulaInfo = formulaInfo.replace(/\s/g, '');
// 错误情况，空字符串
if ('' === formulaInfo) {
    msg.push('数学公式不能为空');
}
// if(/[ii]/.test(formulaInfo)) {
//   msg.push('指标之间必须要有运算符');
// }
if (/^[\x\÷\+\-\*\/]/.test(formulaInfo)) {
    msg.push('不能以运算符开头');
}
//运算符结尾
if (/[\x\÷\+\-\*\/]$/.test(formulaInfo)) {
    msg.push('不能以运算符结尾');
}
//(后面是运算符或者)
// if (/\([\x\÷\+\-\*\/]/.test(formulaInfo)) {
//   msg.push('( 后面是运算符或者 )');
// }
//运算符连续
if (/[\x\÷\+\-\*\/]{2,}/.test(formulaInfo)) {
    msg.push('运算符不能连续');
}
// 空括号
if (/\(\)/.test(formulaInfo)) {
    msg.push('不允许空括号');
}
//括号不配对
var stack = [];
for (var i = 0, item; i < formulaInfo.length; i++) {
    item = formulaInfo.charAt(i);
    if ('(' === item) {
    stack.push('(');
    } else if (')' === item) {
    if (stack.length > 0) {
        stack.pop();
    } else {
        stack.push(')');
    }
    }
}
if (0 !== stack.length) {
    msg.push('括号不配对');
}
//(后面是运算符
if (/\([\x\÷\+\-\*\/]/.test(formulaInfo)) {
    msg.push('( 后面不能是运算符');
}
//)前面是运算符
if (/[\x\÷\+\-\*\/]\)/.test(formulaInfo)) {
    msg.push(') 前面不能是运算符');
}
if (formulaInfo.indexOf('(') != -1) {
    //(前面不是运算符

    if (/(?<!\x|\÷|\+|\-|\*|\/)\b\(/.test(formulaInfo)) {
    msg.push('( 前面必须是运算符');
    } else if (/(?<=\))\(/.test(formulaInfo)) {
    msg.push('( 前面必须是运算符');
    }
    // if (!/[\x\÷\+\-\*\/]\(/.test(formulaInfo)) {
    //   if (!/^\(/.test(formulaInfo)) {
    //     msg.push('( 前面不是运算符');
    //   }
    // }
}
if (formulaInfo.indexOf(')') != -1) {
    //)后面不是运算符
    if (/\)\b(?!\x|\÷|\+|\-|\*|\/)/.test(formulaInfo)) {
    msg.push(') 后面必须是运算符');
    } else if (/\)(?=\()/.test(formulaInfo)) {
    msg.push(') 后面必须是运算符');
    }
    // if (!/\)[\x\÷\+\-\*\/]/.test(formulaInfo)) {
    //   if (!/\)$/.test(formulaInfo)) {
    //     msg.push(') 后面不是运算符')
    //   }
    // }
}
// if(/[\)i]/.test(formulaInfo)) {
//   msg.push(') 和指标之间必须要有运算符');
// }
// if(/[i\(]/.test(formulaInfo)) {
//   msg.push('指标和 ( 之间必须要有运算符');
// }
// var formulaInfoAry = formulaInfoAryJy(formulaInfo);
// for (var i = 0; i < formulaInfoAry.length; i++) {
//   var flag = false;
//   for (var j = 0; j < numberzdm.length; j++) {
//     if (formulaInfoAry[i] == numberzdm[j]) {
//       flag = true;
//       break;
//     }
//   }
//   if (flag == false) {
//     showMsg('' + msg);
//     msg += '<p>字段名不存在或数据型不为数字,不能参与公式计算!</p>';
//   }
// }
if (msg.length !== 0) {
    return msg;
}
// return formulaInfo;
return true;
};
/**
 * 根据图片的url转换对应的base64值
 * @param { String } imageUrl 如：http://xxxx/xxx.png
 * @returns base64取值
 */
export const urlToBase64 = async (imageUrl: string) => {
return new Promise((resolve, reject) => {
    let canvas: HTMLCanvasElement | null = document.createElement('canvas');
    let ctx = canvas.getContext('2d');
    let img: HTMLImageElement | null = new Image();
    if (ctx && img && canvas) {
    img.crossOrigin = 'Anonymous'; // 解决Canvas.toDataURL 图片跨域问题
    img.src = imageUrl;
    img.onload = function () {
        if (!ctx || !img || !canvas) {
        return;
        }
        canvas.height = img.height;
        canvas.width = img.width;
        ctx.fillStyle = '#fff'; // canvas背景填充颜色默认为黑色
        ctx.fillRect(0, 0, img.width, img.height);
        ctx.drawImage(img, 0, 0); // 参数可自定义
        const dataURL = canvas.toDataURL('image/png', 1); // 获取Base64编码
        resolve(dataURL);
        canvas = null; // 清除canvas元素
        img = null; // 清除img元素
    };
    img.onerror = function () {
        reject(new Error('图片加载错误 ' + imageUrl));
    };
    }
});
};