import axios from 'axios'
import { ElLoading, ElMessage, ElMessageBox  } from 'element-plus'
import jwtDecode from 'jwt-decode'
import type { InternalAxiosRequestConfig,AxiosResponse } from 'axios'
import router from '@/router'
import store from '@/store/index'
import { parseWithUndefinedHandling, linkJump } from './common'
import { AxiosCanceler } from './AxiosCanceler' 
import CookieManager from './cookie'
const expireLicenseCodes = [10020,10100,10200,10300]
const Axios = axios.create({
    baseURL:process.env.VUE_APP_BASE_URL,
    timeout: 20000,
    headers: { 'content-type': 'application/json;charset-utf-8'}
})
//loading
let loading:any
const startLoading = () => {
    loading = ElLoading.service({
        text:'拼命加载中',
        spinner:'',
        background:'rgba(0, 0, 0, 0.7)'
    })
}
const endLoading = () => {
    loading.close()
}
//不添加loading的接口,防止页面卡顿
let noLoadingUrl = ['/dataModeling/getDatasourceInfo/']
// 创建 AxiosCanceler 的实例
const canceler = new AxiosCanceler();
// 阻止接口调用标志
let blockRequest = false
export const setBlockRequest = (val:boolean) => {
    blockRequest = val
}
//请求拦截
Axios.interceptors.request.use((config:InternalAxiosRequestConfig):any  => {
    if(blockRequest && !['/user/verifyLicense','user/getSsouser/','user/login'].includes(config?.url!)){
        return new Promise(() => {});
    }
    // 在请求发送前，添加请求到 pendingMap
    canceler.addPending(config);
    if(localStorage.getItem('token') && typeof config.headers?.set === 'function'){//是否登录
        config.headers.set('token', `${localStorage.getItem('token')}`);//头部加上认证信息
        config.headers.set('current-user-id',parseWithUndefinedHandling(localStorage.getItem('user_id')) || -1)
        config.headers.set('current-workspace-id',parseWithUndefinedHandling(sessionStorage.getItem('curWorkspace') || "null")?.id || -1)
    }else{
        router.push({path:'/login'})
    }
    let needLoading = noLoadingUrl.some((item:any) => {
        return  item.indexOf(config.url) == -1
    })
    needLoading && startLoading()
    //修改接口加载时的文字
    if(config.url?.includes('/start/')){
        loading.setText('拼命启动中')
    }
    return config
},(error) => {
    loading && endLoading()
    return Promise.reject(error)
})

//响应拦截
Axios.interceptors.response.use((response:AxiosResponse):AxiosResponse<any> =>{
    loading && endLoading()
    //token失效
    if(response.data.code === 10022){
        ElMessage({
            message: response.data.message,
            type: 'error',
        }) 
        store.commit('CLEAR_CACHE')
        CookieManager.deleteCookie('casdoor_session_id')
        router.push('/login')
    }
    // license过期处理
    if (expireLicenseCodes.includes(response?.data?.code)) {  
        canceler.removeAllPending()
        if(blockRequest){
            return response
        }
        blockRequest = true
        store.commit('users/SET_INVALID_LICENSE',response?.data?.code || 10020)
        ElMessageBox.alert(
            response?.data?.message,
            'license过期提示',
            {
            confirmButtonText: '更新license',
            callback: () => {
                linkJump({path:store.state.users.invalidLicenseInfo?.licensePath,router:router})
            },
          })
    }
    // 在响应完成后，移除请求从 pendingMap
    canceler.removePending(response.config);
    return response
},(error) => {
    loading && endLoading()
    // const { config } = error
    // config && canceler.removePending(config);
    return Promise.reject(error)
})

export {Axios}