import {Axios} from '../../utils/http'
import router from '../../router'
import {sortFunction,parseWithUndefinedHandling} from '../../utils/common'


const state={
    dataDevList:[],
    log:'',//日志
    pageInfo:{
        pageSize:20,
        currentPage:1,
        pageCount:1,
        total:0,
    },
}
const getters = {}
const mutations = {
    SET_DATA_DEV_LIST(state:any,payload:any){
        state.dataDevList = payload
        sortFunction(state.dataDevList,'updated_at',false)//按降序排序
    },
    SET_PAGE_INFO(state:any,{pageSize = state.pageInfo.pageSize,currentPage = state.pageInfo.currentPage,pageCount = state.pageInfo.pageCoun,total = state.pageInfo.total}:{ pageSize?: number, currentPage?: number, pageCount?: number, total?: number }){
        state.pageInfo = Object.assign(state.pageInfo,{pageSize,currentPage,pageCount,total})
    }
}
const actions = {
    getDataDevList(context:any,payload?:any){
        let data:any
        data = {page:context.state.pageInfo.currentPage,
                size:context.state.pageInfo.pageSize,
                searchContent:payload?.searchContent,
                workspace_id:context.rootState.rightsManagement.curWorkspace.id,
                owner_id:parseWithUndefinedHandling(localStorage.getItem('user_id'))}
        Axios({
            method:'post',
            url:'/dataDev/getAll',
            data:data
        }).then((res) => {
            if(res.data.code == 10000){
                context.commit('SET_DATA_DEV_LIST',res.data.data.items)
                context.commit('SET_PAGE_INFO',{total:res.data.data.total,pageCount:Math.ceil(res.data.data.total / res.data.data.size)})
            }else{
                context.commit('SET_DATA_DEV_LIST',[])
                context.commit('SET_PAGE_INFO',{pageSize:10,currentPage:1,pageCount:1,total:0,})
            }
        }).catch((err) => {
            console.log('err',err)
        })
    },
    getDataDevDetails(context:any,payload?:any){
        return  Axios({
            method:'get',
            url:`/dataDev/getDetails/${payload.id}`,
        })
    },
    createDataDev(context:any,payload?:any){
        let data:any
        data = parseWithUndefinedHandling(JSON.stringify(payload))
        data.owner_id = parseWithUndefinedHandling(localStorage.getItem('user_id'))
        data.workspace_id = context.rootState.rightsManagement.curWorkspace.id
        return Axios({
            method:'post',
            url:'/dataDev/create',
            data:data
        }).then((res) => {
            if(res.data.code == 10000){
                context.dispatch('getDataDevList')
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})   
            }else{
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
            }
        }).catch((err) => {
            console.log('err',err)
        })
    },
    updateDataDev(context:any,payload?:any){
        return Axios({
            method:'post',
            url:`/dataDev/update/${payload.id}`,
            data:payload
        }).then((res) => {
            if(res.data.code == 10007){
                context.dispatch('getDataDevList')
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
            }else{
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
            }
        }).catch((err) => {
            console.log('err',err)
        })
    },
    deleteDataDev(context:any,payload?:any){
        Axios({
            method:'delete',
            url:`/dataDev/delete/${payload.id}`,
        }).then((res) => {
            if(res.data.code == 10005){
                const filteredDataDevList = context.state.dataDevList.filter((item:any) => {
                    return item.id != payload.id
                 })
                context.commit('SET_DATA_DEV_LIST',filteredDataDevList)
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
            }else{
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
            }
       }).catch((err) => {
          console.log('err',err)
       })
    },
    startDataDevTask(context:any,payload?:any){
        Axios({
            method:'get',
            url:`/dataDev/start/${payload?.id}`,
        }).then((res) => {
                if(res.data.code == 10009){
                    context.dispatch('getDataDevList')
                    context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
                }else{
                    let message = res.data.message.replace(/\n/g, "<br>")
                    context.commit('POP_MESSAGE',{message: message, type: 'error'},{root:true})
                }
        }).catch((err) => {
            console.log('err',err)
        })
    },
    stopDataDevTask(context:any,payload?:any){
        Axios({
            method:'get',
            url:`/dataDev/stop/${payload?.id}`,
        }).then((res) => {
            if(res.data.code == 10011){
                context.dispatch('getDataDevList')
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
            }else{
                context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
            }
        }).catch((err) => {
            console.log('err',err)
        })
    },
    viewLog(context:any,payload?:any){
        Axios({
            method:'get',
            url:`/dataDev/log/${payload.id}`,
        }).then((res) => {
                context.state.log = res.data
        }).catch((err) => {
            console.log('err',err)
        })
    }
}
export default {
   namespaced:true,
   state,
   getters,
   mutations,
   actions
}