import {Axios} from '../../utils/http'

const state={
    servers:'',
    langType:'',
    notes:'',
    noteContent:'',
}
const getters = {}
const mutations = {}
const actions = {
    getServers(context:any,payload?:any){
        return Axios({
            method:'post',
            url:'dataAnalysis/listAvailableScriptServers',
        }).then((res) => {
            if(res.data.code == 10000){
                context.state.servers = res.data.data
            }
        }).catch((err) => {
            console.log('err',err)
        })
    },
    getNotes(context:any,payload?:any){
        return Axios({
            method:'post',
            url:`dataAnalysis/listScripts`,
            data:{
                script_type: "zeppelin",
                workspace:context.rootState.rightsManagement.curWorkspace.name,
              }
        }).then((res) => {
            if(res.data.code == 10000){
                context.state.notes = res.data.data
            }
        }).catch((err) => {
            console.log('err',err)
        })
    },
    getNoteContent(context:any,payload?:any){
        return Axios({
            method:'post',
            url:`dataAnalysis/getScriptContent`,
            data:{
                    script_type: "zeppelin",
                    language_type: "sql",
                    script_id: payload.noteId,
                    workspace:context.rootState.rightsManagement.curWorkspace.name,
            }
        })  
    },
    getZeppelinUrL(context:any){
        return Axios({
            method:'get',
            url:`dataDev/sqlEditor/${context.rootState.rightsManagement.curWorkspace.name}`,
        })
    }
}
export default {
   namespaced:true,
   state,
   getters,
   mutations,
   actions
}