import axios from 'axios';
import { Formula_Type } from '@/utils/dataIndexs';
import { Axios } from '@/utils/http';
import store from '@/store';

const state = {
  drawer_status: { status: false, type: '', use: '', action: '' }, // drawer的编辑状态是否启动
  iframe_info: {status: false, url: ''},
  // pro为正式版本 pro-chartUrl为测试chart的superset的iframe
  env: 'pro',
};
const getters = {
};
const mutations = {
  set_iframe_info(state:any, payload:any) {
    if(payload === null) {
      state.iframe_info = {status: false, url: ''}
    }else {
      state.iframe_info = payload;
    }
  },
  set_html_status(state: any, payload: 'index' | 'report') {
    state.html_status = payload;
  },
  set_drawer_status(
    state: any,
    payload: {
      status: boolean;
      type: 'add' | 'edit' | '';
      use?: 'index-directory-setting' | 'edition-table-setting' | '';
      action?: 'confirm' | 'cancel' | 'reset' | '';
    }
  ) {
    if (payload === null) {
      state.drawer_status = { status: false, type: '', use: '', action: '' };
    }
    state.drawer_status = {
      ...state.drawer_status,
      ...payload,
    };
  },
};

const actions = {
  async action_get_chartUrl(context: any, payload: { chart_name: string }) {
    return Axios({
      method: 'get',
      url: `/index/getChartUrl/${payload.chart_name}`,
    }).then((res: any) => {
      return res.data.data;
    });
  },
  async action_get_getDashboardUrl(context: any, payload: { chart_name: string }) {
    return Axios({
      method: 'get',
      url: `/index/getDashboardUrl/${payload.chart_name}`,
    }).then((res: any) => {
      return res.data.data;
    });
  },
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
};
