import {Axios} from '../../utils/http'
import {sortFunction,parseWithUndefinedHandling} from '../../utils/common'
const state={
    dataLakeExportList:[],
    alldataLakeExportList:[],
    pageInfo:{
        pageSize:20,
        currentPage:1,
        pageCount:1,
        total:0,
    },
    env_id:-1,
     //调度历史分页
   jobsPageInfo:{
      pageSize:10,
      currentPage:1,
      pageCount:1,
      total:0,
   },
}
  
const getters = {}
const mutations = { 
   SET_DATA_LAKE_EXPORT_LIST(state:any,payload:any){
      state.dataLakeExportList = payload
      sortFunction(state.dataLakeExportList,'updated_at',false)//按降序排序
   },
   SET_PAGE_INFO(state:any,{pageSize = state.pageInfo.pageSize,currentPage = state.pageInfo.currentPage,pageCount = state.pageInfo.pageCoun,total = state.pageInfo.total}:{ pageSize?: number, currentPage?: number, pageCount?: number, total?: number }){
      state.pageInfo = Object.assign(state.pageInfo,{pageSize,currentPage,pageCount,total})
   } 
}
const actions = {
   getDataLakeExportList(context:any,payload:any){
      let data:any
      data = {page:context.state.pageInfo.currentPage,
         size:context.state.pageInfo.pageSize,
         searchContent:payload?.searchContent,
         workspace_id:context.rootState.rightsManagement.curWorkspace.id,
         owner_id:parseWithUndefinedHandling(localStorage.getItem('user_id'))}
      Axios({
         method:'post',
         url:'/dataLakeExport/getAll',
         data:data
      }).then((res) => {
            if(res.data.code == 10000){
               context.commit('SET_DATA_LAKE_EXPORT_LIST',res.data.data.items)
               context.commit('SET_PAGE_INFO',{total:res.data.data.total,pageCount:Math.ceil(res.data.data.total / res.data.data.size)})
            }else{
               context.commit('SET_DATA_LAKE_EXPORT_LIST',[])
               context.commit('SET_PAGE_INFO',{pageSize:10,currentPage:1,pageCount:1,total:0,})
            }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   getDataLakeExportDetails(context:any,payload:any){
      return Axios({
         method:'get',
         url:`/dataLakeExport/getDetails/${payload.id}`,
      })
   },
   createDataLakeExportTask(context:any,payload:any){
      let data:any
      data = JSON.parse(JSON.stringify(payload.curDataLakeExport))
      data.owner_id = parseWithUndefinedHandling(localStorage.getItem('user_id')!)
      data.workspace_id = context.rootState.rightsManagement.curWorkspace.id
      return Axios({
         method:'post',
         url:'/dataLakeExport/create',
         data:data
      }).then((res) => {
           if(res?.data?.code == 10000){
            context.dispatch('getDataLakeExportList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
           return res
      }).catch((err) => {
         console.log('err',err)
      })
   },
   deleteDataLakeExportTask(context:any,payload:any){
      Axios({
         method:'delete',
         url:`/dataLakeExport/delete/${payload.id}`,
      }).then((res) => {
           if(res.data.code == 10005){
            context.dispatch('getDataLakeExportList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   updateDataLakeExportTask(context:any,payload:any){
      Axios({
         method:'post',
         url:`/dataLakeExport/update/${payload.curDataLakeExport.id}`,
         data:payload.curDataLakeExport
      }).then((res) => {
           if(res.data.code == 10007){
            context.dispatch('getDataLakeExportList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   getDataLakeExportStatus(context:any,payload:any){
      Axios({
         method:'get',
         url:`/dataLakeExport/getStatus/${payload.row.id}`,
      }).then((res) => {
           if(res.data.code == 10015){
            payload.row.status = res.data.data.status
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   startDataLakeExportTask(context:any,payload:any){
      Axios({
         method:'get',
         url:`/dataLakeExport/start/${payload.row.id}`,
      }).then((res) => {
           if(res.data.code == 10009){
            // payload.row.status = res.data.data.status
            // context.dispatch('getDataLakeStatus',payload)
            context.dispatch('getDataLakeExportList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   stopDataLakeExportTask(context:any,payload:any){
      Axios({
         method:'get',
         url:`/dataLakeExport/stop/${payload.row.id}`,
      }).then((res) => {
           if(res.data.code == 10011){
            // payload.row.status = res.data.data.status
            // context.dispatch('getDataLakeStatus',payload)
            context.dispatch('getDataLakeExportList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   resumeTask(context:any,payload:any){
      Axios({
         method:'get',
         url:`/dataLakeExport/resume/${payload.row.id}`,
      }).then((res) => {
           if(res.data.code == 10029){
            context.dispatch('getDataLakeExportList')
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'success'},{root:true})
           }else{
            context.commit('POP_MESSAGE',{message: res.data.message, type: 'error'},{root:true})
           }
      }).catch((err) => {
         console.log('err',err)
      })
   },
   viewLog(context:any,payload:any){
      return  Axios({
         method:'get',
         url:`/dataLakeExport/log/${payload.id}`,
      })
   },
   getHistoryJobs(context:any,payload:any){
      return  Axios({
         method:'post',
         url:`/dataLakeExport/getHistoryJobs`,
         data:{
            id:payload.id,
            page:context.state.jobsPageInfo.currentPage,
            size:context.state.jobsPageInfo.pageSize,
         }
         }).then((res) => {
            context.state.historyJobs = res.data.data.items
            context.state.jobsPageInfo.total = res.data.data.total
            context.state.jobsPageInfo.pageCount = Math.ceil(res.data.data.total / res.data.data.size)
         }).catch((err) => {
            console.log('err',err)
         })
   },
   retryHistoryJob(context:any,payload:any){
      return  Axios({
         method:'get',
         url:`/dataLakeExport/retryHistoryJob/${payload.id}`,
      })
   },
   getHistoryJobLog(context:any,payload:any){
      return  Axios({
         method:'post',
         url:`/dataLakeExport/historyJobLog/`,
         data:{
            job_id: payload.job_id,
            app_id: payload.app_id
         }
      })
   },
}
export default {
   namespaced:true,
   state,
   getters,
   mutations,
   actions
}