/**
 * 通过一个指标详情data获取字段
 * @param data
 * @returns
 */
export const getFieldSelectionByIndexDetailData = (data: any) => {
  if (!data) {
    return [];
  }
  let arr = [];
  for (const key in data) {
    if (key === 'time_series_column') {
      // 时间列
      arr.push({
        name: data[key],
        type: 2,
      });
    } else if (key === 'metrics_columns') {
      // 聚合列
      data[key].forEach((it: any) => {
        arr.push({
          name: it.column_name,
          type: 0,
          formula: Formula_Type.indexOf(it.aggregate),
        });
      });
    } else if (key === 'groupby_columns') {
      // 分组列
      data[key].forEach((it: any) => {
        arr.push({
          name: it,
          type: 1,
        });
      });
    }
  }
  return arr;
};
/**
 * 原始获取node的路径名称的方式（原始目录为多级目录）
 * @param node node节点
 * @param path 累计路径，因为该方法为递归
 * @param isAdd 开头是否要加 /
 * @param isIndex 是否加上传过来的node的名称，如销售流程/销售量 传node为销售量 如果isIndex为true 结果为销售流程/销售量 否则为销售流程
 * @returns
 */
export const getPathText = (
  node: Node,
  path: String,
  isAdd: Boolean,
  isIndex?: Boolean
): String => {
  if (node.parent) {
    // 父节点存在
    let _path = path;
    if (isAdd) {
      _path = '/' + node.data.name + path;
    }
    if (isIndex) {
      return (
        getPathText(node.parent, _path, true) +
        (getPathText(node.parent, _path, true) ? '/' : '') +
        node.data.name
      );
    }
    return getPathText(node.parent, _path, true);
  } else {
    if (isIndex) {
      return path.slice(1) + (path.slice(1) ? '/' : '') + node.data.name;
    }
    return path.slice(1);
  }
};
/**
 * 获取某个指标的（目录id、目录名称、指标名称）
 * @param id
 * @param index_type
 * @returns
 */
export const getIndexInfoById = (id: any) => {
  let arr: any[] = store.state.dataIndex.index_dirs.children;
  for (let i = 0; i < arr.length; i++) {
    for (let j = 0; j < arr[i].children.length; j++) {
      if (id == arr[i].children[j].id) {
        // 找到了
        return {
          parent_id: arr[i].id,
          parent_name: arr[i].name,
          name: arr[i].children[j].name,
          id,
          index_type: arr[i].children[j].index_type,
        } as DataIndex_Index_Info;
      }
    }
  }
  return null;
};
