
const state = {
  drag_item_data: null 
};
const getters = {};
const mutations = {
  set_drag_item_data(state:any, payload:{fromData: object, toData: string}|null) {
    if(!payload) {
      state.drag_item_data = null;
    }else {
      state.drag_item_data = payload;
    }
  }
};

const actions = {};
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
};
