<template>
  <div class="licenseEntrance">
    <div class="liceseCard">
      <p class="title">LICENSE</p>
      <el-form :model="licenseForm">
        <el-form-item>
          <el-input v-model="licenseForm.license" type="textarea" />
        </el-form-item>
      </el-form>
      <el-button
        type="primary"
        class="loginBtn"
        @click="toVerify"
        placeholder="Please input license"
        :disabled="[null,undefined,''].includes(licenseForm.license)"
        >验证
      </el-button>
      <div class="bottom">
        <p class="link-text" @click="goBack" style="margin-left: 10px;cursor: pointer;">返回</p>
        <el-tooltip
          class="box-item"
          placement="bottom"
          effect="light"
          :content="invalidLicenseInfo?.template"
          raw-content
        >
          <a class="link-text" :href="invalidLicenseInfo?.cloudSite" target="_blank" style="margin-right: 10px;">还没有 license ?</a>
        </el-tooltip>

      </div>
    </div>
    <SkyLand class="skyLand"></SkyLand>
  </div>
</template>
<script setup>
import { ref, reactive, computed, onBeforeMount } from "vue";
import { useStore } from "vuex";
import { useRouter } from 'vue-router'
import SkyLand from '../components/SkyLand.vue'
const store = useStore();
const router = useRouter()
let licenseForm = reactive({ license: "" });
onBeforeMount(() => {
  store.dispatch('users/getLicenseType').then((res) => {
    store.commit('users/SET_INVALID_LICENSE',res.data?.data?.license_type || 10020)
  }).catch((err) => {
    console.log(err)
  })
})
const toVerify = () => {
  store.dispatch("license", licenseForm);
};
const goBack = () => {
  router.go(-1)
}
const invalidLicenseInfo = computed(() => {
    return store.state.users.invalidLicenseInfo
})

</script>

